            main {
                @media screen and (max-width: 500px) {
                    height: 100vh;
                    overflow: auto;
                }
            }
            
            header {
                display: flex;
                flex-direction: row;
                height: 80pt;
                background: $black-blue;
                flex-shrink: 0;
                align-items: center;
                padding: 0 2em;
                justify-content: space-between;
                nav {
                    display: flex;
                    flex-direction: row;
                    flex: 1 0 auto;
                    justify-content: flex-end;
                    .nav {
                        display: flex;
                        flex-direction: row;
                        a {
                            display: block;
                            margin: 2em;
                            font-size: 16pt;
                            line-height: 22pt;
                            font-family: "DM Sans", sans-serif;
                            font-weight: 700;
                            color: rgba(112, 112, 112, 36%) !important;
                            &:hover {
                                color: $white !important;
                            }
                            &.active {
                                color: $white !important;
                            }
                            .icon_text {
                                content: "";
                            }
                        }
                    }
                }
                @media screen and (max-width:500px) {
                    height: 64pt;
                    order: 2;
                    background: rgba(0, 0, 0, 0);
                    padding: 0;
                    &::after {
                        background: transparent;
                    }
                    .logo {
                        display: none;
                    }
                    .desktopbtn {
                        display: none;
                    }
                    nav {
                        &:after {
                            position: absolute;
                            background: transparent;
                        }
                        display: flex;
                        flex-direction: row;
                        justify-content: center !important;
                        align-items: center;
                        .nav {
                            &::after {
                                background: transparent;
                            }
                            background: $black-blue;
                            flex-grow: 1;
                            justify-content: space-around;
                            &:nth-child(1) {
                                border-top-right-radius: 32px;
                                width: 200px;
                            }
                            &:nth-child(2) {
                                // width: 64pt;
                                position: relative;
                                width: 100px;
                                &::after {
                                    content: "";
                                    position: absolute;
                                    bottom: 30px;
                                    height: 65%;
                                    background: white;
                                    width: 114%;
                                    border-bottom-left-radius: 100px;
                                    border-bottom-right-radius: 100px;
                                }
                            }
                            &:nth-child(3) {
                                border-top-left-radius: 32px;
                                width: 200px;
                            }
                            height: 64pt;
                            align-items: center;
                            a {
                                margin: 0 !important;
                                font-size: 10pt !important;
                                line-height: 20pt !important;
                                font-weight: 400 !important;
                                .icon_text {
                                    display: flex;
                                    flex-direction: column;
                                    align-items: center;
                                    span {
                                        width: 27px;
                                        height: 27px;
                                    }
                                }
                            }
                        }
                    }
                    .mobilebtn {
                        width: 64pt;
                        height: 64pt;
                        border-radius: 50%;
                        border: none;
                        color: $white;
                        background: $red;
                        font-size: 34pt;
                        position: relative;
                        top: -40px;
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        align-items: center;
                        z-index: 1;
                    }
                }
            }