.list-item {
     ::marker {
        content: counters(list-item, '.') !important;
    }
    list-style: inside;
    @media screen and (max-width: 500px) {
        padding-left: 2em;
    }
    .li {
        @media screen and (max-width: 500px) {
            margin-bottom: 0.67em;
        }
        span {
            padding-left: 2em;
        }
        .list-item-li {
            counter-reset: inherit;
        }
    }
}