@import "../../assets/scss/colors/index.scss";
.ant-select-selector {
    padding-left: 0 !important;
}

.card-deals {
    width: 200pt;
    border-radius: 16pt !important;
    background: rgb(228, 229, 230) !important;
    text-align: left;
}

.ant-card-cover {
    padding: 24px;
}

.iconposition {
    svg {
        width: 21px;
        height: 21px;
    }
}

.searchicondeals {
    svg {
        width: 32px;
        height: 32px;
        cursor: pointer;
        color: #01223A;
    }
}

.ant-collapse-header {
    display: flex;
}

.card-container {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    transition: all 0.5s ease;
    justify-content: space-between;
    &.vertical {
        flex-direction: row;
        gap: 24pt;
        @media screen and (max-width: 500px) {
            & {
                justify-content: center;
            }
        }
        .card-item {
            width: 424pt;
            height: 124pt;
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 12pt;
            border-radius: 16pt;
            background-color: rgba(235, 237, 239);
            gap: 16pt;
            @media screen and (max-width: 500px) {
                width: 343pt;
                height: 124pt;
            }
            .card-item-image {
                width: 100pt;
                height: 100pt;
                img {
                    width: 100%;
                    object-fit: contain;
                    height: 100%;
                    border-radius: 8pt;
                }
            }
            .card-item-text {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                height: 100%;
                text-align: start;
                p {
                    font-size: 12pt;
                    line-height: 18pt;
                    font-family: "DM Sans", sans-serif;
                    font-weight: 200;
                    color: $black-blue;
                }
                .description {
                    font-size: 18pt;
                    line-height: 24pt;
                }
                .status {
                    background-color: $black-blue;
                    color: $white;
                    border-radius: 40pt;
                    display: inline;
                    text-align: center;
                }
                .title {
                    color: $blue;
                }
                .name {
                    color: rgba(1, 34, 58, 48%);
                }
            }
        }
    }
    &.horizontal {
        flex-direction: row;
        gap: 24pt;
        @media screen and (max-width: 500px) {
            gap: 16pt;
        }
        .card-item {
            width: 200pt;
            height: 316pt;
            display: flex;
            flex-direction: column;
            padding: 12pt;
            background-color: rgba(235, 237, 239);
            gap: 11pt;
            border-radius: 16pt;
            @media screen and(max-width: 500px) {
                width: 164pt;
                height: 280pt;
            }
            .card-item-image {
                width: 176pt;
                height: 176pt;
                @media screen and (max-width: 500px) {
                    width: 140pt;
                    height: 140pt;
                }
                img {
                    width: 100%;
                    object-fit: contain;
                    height: 100%;
                    border-radius: 8pt;
                }
            }
            .card-item-text {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                text-align: start;
                flex-grow: 1;
                p {
                    font-size: 12pt;
                    line-height: 18pt;
                    font-family: "DM Sans", sans-serif;
                    font-weight: 200;
                    color: $black-blue;
                }
                .description {
                    font-size: 18pt;
                    line-height: 24pt;
                }
                .status {
                    background-color: $black-blue;
                    color: $white;
                    border-radius: 40pt;
                    display: inline;
                    text-align: center;
                    max-width: 72pt;
                }
                .title {
                    color: $blue;
                }
                .name {
                    color: rgba(1, 34, 58, 48%);
                }
            }
        }
    }
}