@import '../../assets/scss/variables/colors.scss';
.privacy-container {
    width: 100%;
    max-width: 824pt;
    margin: auto;
    margin-top: 8pt;
    @media screen and (max-width: 500px) {
        padding: 0 16pt;
    }
    .privacy-item {
        margin-top: 34pt;
        .privacy-title {
            font-size: 58pt;
            font-family: "Space Grotesk", sans-serif;
            line-height: 58pt;
            color: $black-blue;
            margin-bottom: 48pt !important;
            @media screen and (max-width: 500px) {
                font-size: 48pt;
                line-height: 48pt;
            }
        }
        p {
            color: $black-blue;
        }
        .privacy-paragraph {
            font-size: 24pt;
            line-height: 26pt;
            font-family: "Space Grotesk", sans-serif;
            margin-bottom: 16pt !important;
            @media screen and (max-width: 500px) {
                font-size: 22pt;
                line-height: 28pt;
            }
        }
        .privacy-text {
            font-size: 16pt;
            font-family: "DM Sans", sans-serif;
            line-height: 22pt;
        }
        .privacy-list {
            list-style-position: inside;
            color: $black-blue;
            .privacy-text {
                margin-bottom: 9pt !important;
            }
            .privacy-list-item {
                font-size: 16pt;
                font-family: "DM Sans", sans-serif;
                line-height: 22pt;
            }
            .margin {
                margin-top: 9pt !important;
            }
        }
    }
    .privacy-table {
        margin-top: 72pt;
        width: 824pt;
        @media screen and (max-width: 500px) {
            width: 100%;
            overflow: scroll;
        }
        table {
            table-layout: auto;
            @media screen and (max-width: 500px) {
                width: 842pt;
            }
            td,
            th {
                text-align: left;
                font-size: 16pt;
                line-height: 22pt;
                font-family: "DM Sans", sans-serif;
                color: $black-blue;
            }
            thead {
                color: rgba(1, 34, 58, 48%);
                font-size: 16pt;
                line-height: 22pt;
                font-family: "DM Sans", sans-serif;
                border-bottom: 2px solid rgba(1, 34, 58, 16%);
            }
            tbody {
                tr {
                    border-bottom: 2px solid rgba(1, 34, 58, 16%);
                }
                th,
                td {
                    padding: 12pt 0;
                }
            }
        }
    }
}

.privacy-table-title {
    font-size: 24pt;
    line-height: 26pt;
    font-family: "Space Grotesk", sans-serif;
    width: 824pt;
    color: $black-blue;
    padding: 0 !important;
    margin-bottom: 24pt !important;
    margin-top: 36pt !important;
    caption-side: top;
    @media screen and (max-width: 500px) {
        font-size: 22pt;
        line-height: 28pt;
        max-width: 500pt;
        width: 100%;
    }
}