@import '../../assets/scss/variables/colors.scss';
.notification-btn {
    width: 360pt;
    height: 52pt;
    max-width: 360pt;
    box-sizing: border-box;
    cursor: pointer;
    background: $black-blue;
    color: $white;
    text-transform: uppercase;
    font-size: 16pt;
    line-height: 22pt;
    font-weight: bold;
    font-family: 'DM Sans', sans-serif;
    border: 3px solid $black-blue;
    border-radius: 50pt;
    transition: all 0.3s;
    &:hover {
        background: $white;
        color: $black-blue;
    }
}

