.message-conteiner {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 360pt;
    .message-title {
        font-size: 32pt;
        line-height: 38pt;
        font-family: "Space Grotesk", sans-serif;
        color: $black-blue;
        padding: 0 2em;
        margin-top: 32pt;
    }
    .message-paragraph {
        font-size: 16pt;
        line-height: 22pt;
        font-family: "DM Sans", sans-serif;
