.login-btn {
    width: 128pt;
    height: 52pt !important;
    font-size: 16pt !important;
    line-height: 22pt !important;
    font-weight: bold !important;
    font-family: "DM Sans", sans-serif;
    border-radius: 40px !important;
    text-transform: uppercase;
    background: $red !important;
    color: $white !important;
    &:hover,
    &:focus {
        outline: none !important;
        border: none !important;
    }
    @media screen and (max-width:576px) {
        width: 100%;
    }
}

.flex-checkbox {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 8px;
    align-items: center;
}

.winwincheckbox {
    position: absolute;
    z-index: 0;
    opacity: 0;
    width: 24pt;
    height: 24pt;
    cursor: pointer;
    &~label {
        display: inline-flex;
        align-items: center;
        cursor: pointer;
    }
    &~label::before {
        content: "";
        display: inline-block;
        width: 24pt;
        height: 24pt;
        border: 4px solid rgba(1, 34, 58, 8%);
        border-radius: 6pt;
        margin-right: 0.25em;
        background-repeat: no-repeat;
        background-position: center center;
        background-size: 70% 70%;
    }
    &:checked+label::before {
        border-color: $black-blue;
        background-color: $black-blue;
        background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
    }
}

.googleIcon {
    width: 19.6pt;
    height: 20pt;
    margin-bottom: 2px;
}

.padding-zero {
    padding: 0 !important;
}

.btn-login {
    width: 480px;
    height: 69px;
    cursor: pointer;
    text-transform: uppercase;
    border-radius: 40px !important;
    display: flex;
    flex-direction: row;
    justify-content: space-around !important;
    align-items: center;
    border: none;
    span {
        width: 100%;
    }
    @media screen and (max-width:576px) {
        width: 69px;
    }
}

.google {
    background: rgba(66, 133, 244, 100%) !important;
}

.facebook {
    background: rgba(59, 90, 152, 100%) !important;
    img {
        width: 18px;
        height: 27px;
    }
}

.apple {
    svg {
        width: 55px;
        height: 55px;
    }
}

.microsoft {
    width: 480px;
    height: 69px;
}