@import "../../assets/scss/variables/colors.scss";
.spacing {
    display: flex;
    flex-direction: column;
    max-width: 457px;
    margin: auto;
    margin-top: 19pt;
    .spacing-item {
        flex-grow: 1;
        &:nth-child(1) {
            margin-bottom: 18pt;
        }
        &:nth-child(2) {
            margin-bottom: 36pt;
        }
        .btn-error {
            width: 457px;
            height: 52pt;
            border-radius: 50pt;
            border: 4px solid $black-blue;
            background-color: $black-blue;
            color: $white;
            font-size: 16pt;
            line-height: 22pt;
            font-weight: bold;
            font-family: "DM Sans", sans-serif;
            text-transform: uppercase;
            cursor: pointer;
            transition: all 0.3s;
            &:hover {
                border-color: $black-blue;
                background-color: $white;
                color: $black-blue;
            }
        }
        .error-title {
            font-size: 48pt;
            line-height: 48pt;
            font-family: "Space Grotesk", sans-serif;
        }
        .error-paragraph {
            font-size: 16pt;
            line-height: 22pt;
            font-family: "DM Sans", sans-serif;
            padding: 0 30px;
            margin: 0px;
        }
    }
}

.cookie-msg {
    display: flex;
    flex-direction: column;
    max-width: 457px;
    margin: auto;
    background-color: $last;
    margin-top: 65pt;
    border-radius: 36pt 0 36pt 0;
    margin-bottom: 24pt;
    .cookie-msg-item {
        &:nth-child(1) {
            margin-top: 16pt;
        }
        &:nth-child(2) {
            margin-top: 16pt;
        }
        &:nth-child(3) {
            margin-top: 15pt;
            margin-bottom: 17pt;
            margin-left: 22pt;
        }
        max-width: 311pt;
        margin: auto;
        .cookie-msg-title {
            font-size: 15pt;
            line-height: 22pt;
            font-family: "DM Sans", sans-serif;
            text-align: start;
            font-weight: 400;
        }
        .cookie-msg-btn {
            width: 311pt;
            height: 52pt;
            border-radius: 50pt;
            border: 4px solid $black-blue;
            background-color: $black-blue;
            color: $white;
            font-size: 16pt;
            line-height: 22pt;
            font-weight: bold;
            font-family: "DM Sans", sans-serif;
            text-transform: uppercase;
            cursor: pointer;
            transition: all 0.3s;
            &:hover {
                border-color: $black-blue;
                background-color: $white;
                color: $black-blue;
            }
        }
    }
}

.cookie-msg-link {
    color: $blue;
    transition: all 0.3s;
    &:hover {
        text-decoration: 1px solid $blue !important;
    }
}