@import '../../assets/scss/variables/colors.scss';
.dealsmsg-container {
    width: 100%;
    max-width: 872pt;
    height: 84pt;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    border-radius: 0 24pt 0 24pt;
    margin-top: 25pt;
    @media screen and (max-width: 500px) {
        width: 344pt;
        height: 148pt;
        flex-direction: column;
        text-align: center;
    }
    &.approve {
        background-color: $blue;
    }
    &.error {
        background-color: $red;
    }
    .deals-msg-text {
        font-size: 18pt;
        line-height: 24pt;
        font-family: "DM Sans", sans-serif;
        color: $black-blue;
        font-weight: lighter;
    }
    .deals-msg-btn {
        button {
            width: 360pt;
            height: 52pt;
            text-transform: uppercase;
            font-size: 16pt;
            line-height: 22pt;
            font-weight: bold;
            color: $white;
            background-color: $black-blue;
            border-radius: 50pt;
            border: 2px solid $black-blue;
            cursor: pointer;
            transition: all 0.5s ease;
            &:hover {
                color: $black-blue;
                background-color: $white;
            }
            @media screen and (max-width: 500px) {
                width: 312pt;
                height: 52pt;
            }
        }
    }
}